.navbar {
  background: transparent;
  height: 10;
  display: flex;
  justify-content: center;
  align-items: center;

  top: 0;
  z-index: 999;
}

.navbar.active {
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.9) 0%,
    rgba(255, 255, 255, 0.9) 100%
  );
  position: sticky;
  box-shadow:  rgba(15, 15, 15);
}
.nav {
  font-family: "Hammersmith One", sans-serif;
  color: "black";
  font-size: 18px;
  margin-left: 3mm;

  font-weight: 300;
}
.nav.brand{
  align-items: flex-start;
}