.navItems {
  margin-top: 10px;
  margin-bottom: 10px;

  margin-right: 20px;

  font-size: 15px;
  font-weight: 500;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  /* background-color: white; */
  padding-right: 10px;
  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 10px 0px 0px 10px;
  width: 150px;
  float: right;
}

.navItems:hover {
  margin-top: 10px;
  margin-bottom: 10px;

  margin-right: 20px;

  font-size: 15px;
  font-weight: 500;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  /* background-color: white; */
  padding-right: 10px;
  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 10px 0px 0px 10px;
  width: 160px;
  float: right;
}

.deleteAccount:hover {
  border: none;
  color: red;
  background-color: transparent;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

.deleteAccount {
  border: none;
  color: #91091e;
  background-color: transparent;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}
